.main {
  max-width: $main-width;
  margin: 0 auto; }

#contents {
  min-height: $content-min-height;

  a {
    color: $link-color;
    text-decoration: none;

    &:hover {
      background-color: $link-color;
      color: #fff;
      -webkit-transition-duration: 400ms; } }

  a.title {
    color: desaturate($link-color, 50%);

    &:hover {
      color: #C00475;
      background-color: transparent;
      -webkit-transition-duration: 700ms; } }

  hr {
    border-top: 3px dotted #ccc;
    margin: 20px 0; }

  .meta {
    font-size: $min-fontsize;

    span, a {
      text-decoration: underline;
      color: $grey;

      &:hover {
        border-bottom: none;
        background-color: transparent; } }

    [class^="icon-"] {
      padding-right: 7px; }

    .icon-tags {
      padding-left: 10px; }

    img {
      padding-top: 13px; } }

  .posts {
    margin-bottom: 37px;
    h1 {
      font-size: 1.6rem; } }

  .post {
    margin-bottom: 30px; }

  .heading {
    font-size: 24px;
    text-transform: uppercase;
    margin-top: 10px;
    margin-bottom: 30px;
    font-weight: bold;
    color: #222; }

  h1, h2, h3, h4, h5, h6 {
    color: $dark-black;
    font-feature-settings: "palt"; }

  h1 {
    font-size: 1.8rem;
    line-height: 1.5;
    margin-bottom: 15px;

    @media screen and (max-width: $mobile-width) {
      font-size: 1.5rem; } }

  h2 {
    font-size: 1.5rem;
    margin: 20px 0; }

  h3 {
    font-size: 1.2rem;
    margin: 18px 0 14px; }

  h5 {
    font-size: .9rem;
    margin: 15px 0 10px; }

  @mixin panel {
    position: relative;
    padding: 0 1.5rem;
    margin-top: .5rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: .25rem; }

  .info {
    @include panel;
    color: #155724;
    background-color: #d8eadd;
    border-color: #c3e6cb; }
  p.info, p.warning {
    padding: 1rem; }

  .warn {
    @include panel;
    color: #856404;
    background-color: #fff3cd;
    border-color: #ffeeba; }
  p.warn {
    padding: 1rem; }

  .warning {
    @include panel;
    color: #721c24;
    background-color: #ead8dd;
    border-color: #f5c6cb; }

  hr .separator {
    margin: 25px auto 8px;
    max-width: 280px;
    border-width: 1px 0 0 0;
    @include separator-style;

    &:before {
      display: block;
      content: "";
      margin-top: -31px;
      border-width: 0 0 1px 0;
      @include separator-style; } }

  .post-pagination {
    padding-top: 10px;
    padding-bottom: 30px;
    a {
      text-decoration: none;
      @include pagination-button;
      &:hover {
        border-bottom: none; } }

    .newer {
      float: left;
      width: 100px;
      text-align: left; }

    .older {
      float: right;
      width: 100px;
      text-align: right; } }

  .section-nav {
    font-size: .95em;
    background-color: #ECF2F5;
    padding: 12px 29px;
    // Material Design shadow
    // via. http://materializecss.com/components.html
    -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    -moz-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    a {
      color: darken($link-color, 10%);
      &:hover, &:focus {
        background-color: transparent;
        text-decoration: underline; } } }

  .section-note {
    background-color: #D8E7EF;
    padding: 12px 18px; }

  .footnotes {
    font-size: $small-fontsize;
    padding-top: 10px;
    p {
      margin: 0; } }

  // inspired by pure-css (http://purecss.io/buttons/)
  .btn {
    font-size: 100%;
    padding: .5em 1.5em;
    color: rgba(0, 0, 0, 0.8);
    border: 1px solid $grey;
    border: 0 rgba(0, 0, 0, 0);
    background-color: #E6E6E6;
    border-radius: 3px;
    -webkit-transition: .1s linear -webkit-box-shadow;
    -moz-transition: .1s linear -moz-box-shadow;
    -ms-transition: .1s linear box-shadow;
    -o-transition: .1s linear box-shadow;
    transition: .1s linear box-shadow;

    &:hover, &:focus {
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#1a000000', GradientType=0);
      background-image: -webkit-gradient(linear,0 0,0 100%,from(rgba(0, 0, 0, 0)),color-stop(40%,rgba(0, 0, 0, 0.05)),to(rgba(0, 0, 0, 0.1)));
      background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.05) 40%,rgba(0, 0, 0, 0.1));
      background-image: -moz-linear-gradient(top,rgba(0,0,0,.05) 0,rgba(0,0,0,.1));
      background-image: -ms-linear-gradient(transparent,rgba(0,0,0,.05) 40%,rgba(0,0,0,.1));
      background-image: -o-linear-gradient(transparent,rgba(0,0,0,.05) 40%,rgba(0,0,0,.1));
      background-image: linear-gradient(rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.05) 40%,rgba(0, 0, 0, 0.1)); }

    &:active {
      box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.15) inset,0 0 6px rgba(0, 0, 0, 0.2) inset; } }

  .btn-primary {
    background-color: #0078E7;
    color: #FFF;

    &:hover, &:focus {
      background-color: #0078E7; } } }

#md-content {
  table {
    border-collapse: collapse;
    border-spacing: 0;
    empty-cells: show;
    border: 1px solid #CBCBCB;
    margin-bottom: 18px;

    tbody {
      background-color: white; }

    tr {
      display: table-row;
      vertical-align: inherit;
      border-color: inherit; }

    td, th {
      font-size: inherit;
      margin: 0;
      overflow: visible;
      padding: 6px 12px;
      border-width: 0 0 1px;
      border-bottom: 1px solid #CBCBCB; }

    th {
      font-weight: bold;
      background: #E0E0E0;
      vertical-align: bottom; } } }

#social-buttons {
  padding: 0;
  margin-bottom: 20px;

  li {
    line-height: 0;
    list-style-type: none;
    display: inline-block;
    vertical-align: top; }

  .twitter, .hatena {
    padding-right: 13px; } }

.related {
  ul {
    margin-top: 0px; }
  li {
    font-size: $small-fontsize;
    line-height: 20px; } }
